import React, {Component} from 'react';
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';
// import hero from './hero2.png';
import hero from './hero2-b.jpg';
import styles from './landing.css';

class Landing extends Component {
  componentDidMount(){
    window.scrollTo(0, 0);
  }
  
  render(){
    const landingBackground = {
      // width: "100vw",
      marginTop:'10px',
      height: "85vh",
      backgroundImage: `url(${hero})`,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center bottom' 
      
    }
    const secondBackground = {
      width: "100vw",
      height: "100vh",
      backgroundColor: "#FFEB3B",
      position: 'relative',
      padding: '50px'
    }
    const thirdBackground = {
      width: "100vw",
      height: "60vh",
      backgroundColor: "#de87f6",
      position: 'relative',
    }
    const fourthBackground = {
      width: "100vw",
      height: "100vh",
      backgroundColor: "#87f6c0",
      position: 'relative',
    }
    const centerText = {
      position: 'absolute',
      left: '50%',
      top: '50%',
      WebkitTransform: 'translate(-50%, -50%)',
      transform: 'translate(-50%, -50%)',
      color: 'black',
    }
    const secondIcons = {
      height: '80%',
      width: '80%'
    }

    return (
      <div id="landing">
        <div style={landingBackground}>
          <div style={{position:'absolute'}}>
          <p className="legend">BE&nbsp; A&nbsp; LEGEND.</p>
          </div>
        </div>
        <div className='container-fluid' style={secondBackground}>
          <div className='container text-center' style={centerText}>
            <h1>Statistics</h1>
          </div>
        </div>
        <div className='container-fluid' style={thirdBackground}>
          <div className='container text-center' style={centerText}>
            <h1>Statistics</h1>
          </div>
        </div>
        <div className='container-fluid' style={fourthBackground}>
          <div className='container text-center' style={centerText}>
            <h1>About us</h1>
          </div>
        </div>
      </div>
    )
  }
}

export default Landing;



