import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { logOut } from '../../actions'
import logoGlobe from './logo-globe.png';
import styles from './header.css'

class Header extends Component {
    
  logOut = () => {
    this.props.logOut();
    this.props.history.push('/')    
  }

  render(){
    const firstName = localStorage.getItem('firstName');

    let loginPanel = 
         <Link to={'/signin'} className="navbar-item">Login/Register</Link>;

    if (firstName) {
      loginPanel = 
        <div>               
            <div className="navbar-item has-dropdown is-hoverable">
              <Link to={'/profile'} className="navbar-link">Welcome {firstName}</Link>
              <div className="navbar-dropdown">
              <Link to={'/profile'} className="navbar-item">Profile</Link>      
              <hr className="navbar-divider" />
              <a onClick={this.logOut} className="navbar-item">Logout</a>
            </div>
          </div>
        </div>
    }

    return (

      <nav id="myHeader" className="navbar is-white">
      <div className="navbar-brand">
        <Link to={'/'}>     
          <img src={logoGlobe} alt="Young Founders School: A Start up Bootcamp for High School Students" className="logoGlobe" />
          <div className="brand">
            <p className="title is-4">Young Founders School</p>
            <p className="subtitle is-5">Sponsored by Credit Suisse</p>
          </div>
        </Link>

        <div className="navbar-burger burger" data-target="navMenuColorwhite-example">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    
      <div id="navMenuColorwhite-example" className="navbar-menu">
        <div className="navbar-start">
        </div>
    
        <div className="navbar-end">
              <Link to={'/events'} className="navbar-item">
                Programs
              </Link>
              <Link to={'/contact'} className="navbar-item">
                Gallery
              </Link>
              <Link to={'/contact'} className="navbar-item">
                Contact
              </Link>
              <Link to={'/contact'} className="navbar-item">
                Be a Mentor!
              </Link>
              {loginPanel}
        </div>
      </div>
    </nav>
  
    )
  }
}

const mapStateToProps = state => ({
  loggedIn: state.userInfo.loggedIn,
  currentUser: state.userInfo.currentUser
});

export default withRouter(connect(mapStateToProps, {logOut})(Header) );



